.mid {
    background-color: aqua;
    display: inline-block;
    width: 50%;
    margin: auto;
}

.navbar {

    display: inline-block;
}




.navbar li {

    list-style: none;
    display: inline-block;

    margin: auto;
}

.navbar li a {
    text-decoration: none;
    color: rgb(29, 25, 231);
    font-size: 18px;
    padding: 8px 20px;
}

.navbar li a:hover {
    color: rgb(151, 7, 247);
    border: darkmagenta;
    border-radius: 2px;
    font-size: 20px;
}

.navbar li a:active {
    background-color: deepskyblue;
    border-radius: 5px;
}

